<template>
    <div class="user-list">
        <Provider
            url="/api/user"
            :query="{
                order: 'start:desc',
            }"
            :data-resolve="resolveData"
        >
            <template slot="header" slot-scope="{ list }">
                <ElAlert type="success">
                    历史总用户：{{ list.length }}个，
                    <ElInputNumber
                        v-model="filterDay"
                        size="mini"
                        controls-position="right"
                        :min="1"
                        :max="100"
                        style="width: 90px;margin-right: 4px;"
                        label="描述文字"
                    />
                    天未使用的用户{{ getNoActiveUserList(list).length }}个
                </ElAlert>
            </template>
            <ElTable
                slot-scope="{ list }"
                :data="list"
                stripe
                style="width: 100%"
                :default-sort="{prop: 'start', order: 'descending'}"
            >
                <ElTableColumn prop="name" label="用户名" width="180" />
                <ElTableColumn prop="start" label="操作时间" width="140" sortable>
                    <div slot-scope="{ row }">
                        <ElTag type="warning">{{ row.start | date('MM-dd hh:mm:ss') }}</ElTag>
                    </div>
                </ElTableColumn>
                <ElTableColumn prop="duration" label="耗时" width="100" sortable>
                    <div slot-scope="{ row }">{{ row.duration | duration }}</div>
                </ElTableColumn>
                <ElTableColumn prop="count" label="操作次数" width="100" sortable />
                <ElTableColumn label="详情">
                    <ElButton slot-scope="{ row }" type="text" size="mini" @click="toDetail(row)">用户操作记录</ElButton>
                </ElTableColumn>
            </ElTable>
        </Provider>
    </div>
</template>

<script>
import { axiosGet } from '../libs/request';
import { resolveData } from '../util/business';
import { DAY_MS } from '../constant/time';

export default {
    name: 'UserList',
    data: function () {
        return {
            filterDay: 30,
            lastUserUpdateTime: Date.now(),
        };
    },
    methods: {
        resolveData(result) {
            return {
                ...result,
                data: resolveData(result.data),
            };
        },
        getNoActiveUserList(list) {
            console.log('getNoActiveUserList', { list });
            const now = Date.now();
            return list.filter(item => now - item.start > this.filterDay * DAY_MS);
        },
        toDetail(item) {
            this.$router.push({
                path: this.$route.path,
                query: {
                    page: 'user_info',
                    user: item.name,
                },
            });
        },
    },
};
</script>

