<template>
    <div class="provider">
        <slot name="header" :list="useData.data" />
        <ElPagination
            v-if="topPagination"
            :current-page="currentPage"
            :page-sizes="pageSizeOptions"
            :page-size="currentPageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalCount"
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
        />
        <div>
            <slot :list="useData.data" />
        </div>
        <ElPagination
            :current-page="currentPage"
            :page-sizes="pageSizeOptions"
            :page-size="currentPageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalCount"
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
        />
    </div>
</template>

<script>
import { axiosGet } from '../libs/request';
import { isSameContent, isString } from '../util/is';

const isLegalOptionObj = (item) => {
    if (!item.key) return false;
    const { current } = item;
    if (!current || current.label === undefined || current.value === undefined) return false;
    return Array.isArray(item.list) && item.list.every(obj => obj.label !== undefined && obj.value !== undefined);
};

export default {
    name: 'Provider',

    props: {
        url: {
            type: String,
            default: '',
        },

        defaultData: {
            type: Object,
            default() {
                return {
                    data: [],
                    page: 1,
                    page_size: 20,
                    total_count: 0,
                };
            },
        },

        filterOptions: {
            type: Array,
            default() {
                return [];
            },
        },

        topPagination: {
            type: Boolean,
            default: true,
        },

        query: {
            type: Object,
            default() {
                return {};
            },
        },

        search: {
            type: Object,
            default() {
                return {};
            },
        },

        pageSizeOptions: {
            type: Array,
            default() {
                return [20, 50, 100, 200, 300];
            },
        },

        page: {
            type: Number,
            default: 1,
            validate(val) {
                return Number.isInteger(val) && val > 0;
            },
        },

        pageSize: {
            type: Number,
            default: 20,
            validate(val) {
                return Number.isInteger(val) && val > 0;
            },
        },

        dataResolve: {
            type: Function,
            default() {
                return (data) => data;
            },
        },
    },

    data() {
        return {
            commonFilterOptions: [],
            currentPage: this.page,
            currentPageSize: this.pageSize,
            data: null,
            defaultSearchOption: {
                key: 'q',
                type: 'multiple',
                multiple: true,
                multiplePlaceholder: '输入',
                multipleSeparator: ',，\n',
                placeholder: '关键词、宝贝ID（宝贝链接）',
                onSubmit() {},
            },
            initQuery: {},
            currentSearchText: '',
            filterOptionsList: [],
            totalCount: 0,
            isLoading: true,
            cacheSelectionList: [],
        };
    },

    computed: {
        useData() {
            return this.data || this.defaultData;
        },
        hasConfigSearch() {
            return Object.keys(this.search).length > 0;
        },
        searchOption() {
            return {
                ...this.defaultSearchOption,
                ...this.search,
            };
        },
        filterOptionsQueryParams() {
            return this.filterOptionsList.reduce((obj, item) => {
                obj[item.key] = item.current.value;
                return obj;
            }, {});
        },
        queryParams() {
            return {
                // ...this.initQuery,
                // ...this.$route.query,
                ...this.filterOptionsQueryParams,
                page: this.currentPage,
                page_size: this.currentPageSize,
                // [this.searchOption.key]: this.currentSearchText,
                ...this.query,
            };
        },
    },

    watch: {
        filterOptions: {
            immediate: true,
            handler() {
                this.updateFilterOptionsList();
            },
        },
        commonFilterOptions: {
            deep: true,
            handler() {
                this.updateFilterOptionsList();
            },
        },
        query: {
            handler(val, oldVal) {
                if (isSameContent(val, oldVal)) return;
                this.currentPage = 1;
                this.getList();
            },
            deep: true,
        },
        pageSize(newValue) {
            this.currentPageSize = newValue;
        },
    },
    created() {
        this.initUrlQuery();
        this.getList();
    },

    methods: {
        handleSizeChange(size) {
            this.currentPageSize = size;
            this.getList();
        },
        handleCurrentChange(page) {
            this.currentPage = page;
            this.getList();
        },
        updateFilterOptionsList() {
            this.filterOptionsList = this.filterOptions.map((item) => {
                if (isString(item)) {
                    return this.mapFilterKeyItem(item);
                }
                if (!isLegalOptionObj(item)) {
                    window.console.error('filter option is not legal array', item);
                    return {};
                }
                // 如果选项是符合规范的数组，直接使用
                return item;
            });
        },
        initUrlQuery() {
            const { query } = this.$route;
            Object.keys(query).forEach((queryKey) => {
                const queryValue = query[queryKey];
                const filterItem = this.filterOptions.find(item => item.key === queryKey);

                if (filterItem) {
                    const filterOptionItem = filterItem.list.find(item => item.value == queryValue);

                    if (filterOptionItem) {
                        filterItem.current = filterOptionItem;
                    }
                    return;
                }
                if (queryKey === 'page') {
                    const num = parseInt(queryValue, 10);
                    if (Number.isInteger(num) && num > 0) {
                        this.currentPage = num;
                    }
                    return;
                }
                if (queryKey === 'page_size') {
                    const num = parseInt(queryValue, 10);
                    if (Number.isInteger(num) && num > 0) {
                        this.currentPageSize = num;
                    }
                    return;
                }
                this.initQuery[queryKey] = queryValue;
            });
        },
        mapFilterKeyItem(key) {
            // 复用已有的选项，保留已选择的选项
            const filterItem = this.filterOptionsList.find(option => option.key === key);
            const filterOption = this.commonFilterOptions[key];

            if (filterItem) {
                filterItem.list = filterOption;
                return filterItem;
            }

            return {
                key,
                current: filterOption[0] || {},
                list: filterOption,
            };
        },
        updateUrlQuery() {
            this.$router.replace({
                path: this.$route.path,
                query: this.queryParams,
            });
        },
        optionChange({ filterOption, option }) {
            filterOption.current = option;
            this.currentPage = 1;
            this.getList();
        },
        onSubmit(text) {
            this.currentSearchText = text.replace(/[\n]/g, ',');
            this.searchOption.onSubmit();
            this.currentPage = 1;
            this.getList();
        },
        async getList() {
            this.$emit('before-update', this.queryParams, this);
            this.updateUrlQuery();
            this.isLoading = true;

            axiosGet(this.url, this.queryParams).then((result) => {
                const { total_count: totalCount } = result;
                this.isLoading = false;
                this.totalCount = totalCount;
                this.data = this.dataResolve(result);
                this.$emit('update', result, this);
            }).catch(() => {
                this.isLoading = false;
            });
        },
    },
};
</script>

