export function rand(n, m) {
    return n + Math.floor(Math.random() * (m - n));
}

export function range(val, min, max) {
    const number = parseInt(val, 10);
    if (isNaN(number)) {
        return 1;
    }
    return Math.min(Math.max(number, min), max);
}

export function filterNullValue(obj) {
    return Object.keys(obj).reduce((newObj, item) => {
        const value = obj[item];
        if (value !== undefined && value !== null && value !== '') {
            newObj[item] = obj[item];
        }
        return newObj;
    }, {});
}

export function removeKeys(obj, keys) {
    return Object.keys(obj).reduce((newObj, item) => {
        if (!keys.includes(item)) {
            newObj[item] = obj[item];
        }
        return newObj;
    }, {});
}

export function isMobile() {
    return /Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent);
}

export function numberToK(val) {
    if (val < 1000) {
        return val;
    }
    if (val >= 1000) {
        return (val / 1000).toFixed(1) + 'K';
    }
}

export function createFromN2N(min, max) {
    const len = max - min + 1;
    return Array(Math.max(len, 0)).fill(0).map((_item, i) => i + min);
}

export function dateFormat(time, format = 'yyyy-MM-dd hh:mm:ss') {
    if (!time) return 'null';

    let value = time;

    if (typeof value === 'number') {
        if (String(time).length < 10) {
            value = new Date().getTime();
        } else if (String(time).length === 10) {
            value = time * 1000;
        }
    }

    const date = new Date(value);

    const map = {
        M: date.getMonth() + 1, // 月份
        d: date.getDate(), // 日
        h: date.getHours(), // 小时
        m: date.getMinutes(), // 分
        s: date.getSeconds(), // 秒
        q: Math.floor((date.getMonth() + 3) / 3), // 季度
        S: date.getMilliseconds(), // 毫秒
    };

    return format.replace(/(y+|M+|d+|h+|m+|s+|q+|S+)/g, (all, t) => {
        t = t.length > 1 ? t[0] : t;
        let v = map[t];
        if (v !== undefined) {
            if (all.length > 1) {
                v = `0${v}`;
                v = v.substr(v.length - 2);
            }
            return v;
        } if (t === 'y') {
            return (String(date.getFullYear())).substr(4 - all.length);
        }
        return all;
    });
};
