import { SCENE_MAP, TYPE_MAP } from '../constant/map';

export function resolveData(data) {
    return data.map(item => ({
        ...item,
        scene: SCENE_MAP[item.scene],
        type: TYPE_MAP[item.type],
        duration: item.end - item.start,
    }));
};
