import Vue from 'vue';
import VueRouter from 'vue-router';
import Index from '../views/index.vue';

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        redirect: '/index',
    },
    {
        path: '/index/:page',
        component: Index,
    },
];

const router = new VueRouter({
    routes,
});

router.beforeEach((to, from, next) => {
    if (to.path === '/index' && !to.params.page) {
        next({
            path: '/index/user',
        });
        return;
    }
    next();
});

export default router;
