import OSS from 'ali-oss';

const client = new OSS({
    region: 'oss-cn-hangzhou',
    accessKeyId: 'LTAI4Fp34XZz4JocDhXdmXn9',
    accessKeySecret: 'GxC3UJIFNZg38IDZ3IG2D13Ip4YMqv',
    bucket: 'registration-apply',
});

export async function getJSONData(name) {
    try {
        const result = await client.get(name);
        return JSON.parse(result.content.toString());
    } catch (e) {
        return e ? e.code : 'unknown error';
    }
}
