export function is(val, type) {
    return Object.prototype.toString.call(val).slice(8, -1).toLowerCase() === type;
}

export function isPlainObject(val) {
    return is(val, 'object');
}

export function isString(val) {
    return is(val, 'string');
}

export function isNumber(val) {
    return is(val, 'number');
}

export function isInteger(val) {
    return Number.isInteger(val);
}

export function isBoolean(val) {
    return is(val, 'boolean');
}

export function isArray(val) {
    return Array.isArray(val);
}

export function isNull(val) {
    return is(val, 'null');
}

export function isUndefined(val) {
    return is(val, 'undefined');
}

export function isDate(val) {
    return is(val, 'date');
}

export function isSameContentArray(arr1, arr2) {
    return arr1.every((item, index) => isSameContent(item, arr2[index]));
}

export function isSameContentObject(obj1, obj2) {
    const obj1Keys = Object.keys(obj1);
    const obj2Keys = Object.keys(obj2);
    if (obj1Keys.length !== obj2Keys.length) return false;
    for (const key of obj1Keys) {
        if (!isSameContent(obj1[key], obj2[key])) return false;
    }
    return true;
}

export function isSameContent(val1, val2) {
    if (isPlainObject(val1) && isPlainObject(val2)) return isSameContentObject(val1, val2);
    if (isArray(val1) && isArray(val2)) return isSameContentArray(val1, val2);
    return val1 === val2;
}
