<template>
    <div class="user-info">
        <ElInput
            v-model="currentActivityName"
            placeholder="请输入活动名"
            prefix-icon="el-icon-search"
            style="margin-bottom: 10px;"
            @keydown.enter.native="$refs.provider.getList()"
        />
        <ElInput
            v-model="currentUser"
            placeholder="请输入用户名"
            prefix-icon="el-icon-search"
            @keydown.enter.native="$refs.provider.getList()"
        />

        <Provider
            ref="provider"
            url="/api/user_info"
            :data-resolve="resolveData"
            :query="{
                where: [
                    ...(currentUser ? [['name', 'like', `%${currentUser}%`]] : []),
                    ...(currentActivityName ? [['activity_name', 'like', `%${currentActivityName}%`]] : []),
                ],
                order: 'start:desc',
            }"
        >
            <ElTable
                slot-scope="{ list }"
                :data="list"
                stripe
                style="width: 100%"
                :default-sort="{prop: 'start', order: 'descending'}"
            >
                <ElTableColumn
                    prop="name"
                    label="用户名"
                    width="180"
                >
                    <div slot-scope="{ row }">
                        <p>{{ row.name }}</p>
                        <p class="desc">{{ row.activity_name }}</p>
                    </div>
                </ElTableColumn>
                <ElTableColumn
                    prop="activity_id"
                    label="活动ID"
                    width="120"
                />
                <ElTableColumn
                    prop="scene"
                    label="场景"
                    width="90"
                    :filters="filterOptions.scene"
                    :filter-method="userInfoFilterHandler"
                />
                <ElTableColumn
                    prop="type"
                    label="操作类型"
                    width="120"
                    :filters="filterOptions.type"
                    :filter-method="userInfoFilterHandler"
                />
                <ElTableColumn prop="start" label="操作时间" width="140" sortable>
                    <div slot-scope="{ row }">
                        <ElTag type="warning">{{ row.start | date('MM-dd hh:mm:ss') }}</ElTag>
                    </div>
                </ElTableColumn>
                <ElTableColumn prop="duration" label="耗时" width="100" sortable>
                    <div slot-scope="{ row }">{{ row.duration | duration }}</div>
                </ElTableColumn>
                <ElTableColumn prop="success" label="成功数" width="200" sortable />
                <ElTableColumn prop="error" label="失败数" width="100" sortable />
                <ElTableColumn label="提交数据">
                    <ElButton
                        slot-scope="{ row }"
                        plain
                        type="success"
                        size="mini"
                        @click="toSubmitDetail(row.submit_items_id)"
                    >
                        {{ row.type === '采集' ? '采集' : '提交' }}数据
                    </ElButton>
                </ElTableColumn>
                <ElTableColumn label="失败数据">
                    <ElButton
                        slot-scope="{ row }"
                        plain
                        type="danger"
                        size="mini"
                        @click="toSubmitDetail(row.error_items_id)"
                    >
                        失败数据
                    </ElButton>
                </ElTableColumn>
                <ElTableColumn label="接口数据">
                    <ElButton
                        slot-scope="{ row }"
                        plain
                        type="primary"
                        size="mini"
                        @click="toSubmitDetail(row.request_id)"
                    >
                        接口数据
                    </ElButton>
                </ElTableColumn>
            </ElTable>
        </Provider>
    </div>
</template>

<script>
import { axiosGet } from '../libs/request';
import { FILTER_OPTIONS } from '../constant/filterOptions';
import { resolveData } from '../util/business';

export default {
    name: 'UserInfo',

    props: {},

    data() {
        return {
            currentUser: this.$route.query.user,
            currentActivityName: this.$route.query.activityName,
            filterOptions: FILTER_OPTIONS,
        };
    },

    created() {
        axiosGet('/api/items_log', {
            username: '敦奴官方outlets店',
            page_size: 1000,
            // where: [
            //     ['errors', 'like', '淘宝%']],
        });
    },

    methods: {
        resolveData(result) {
            return {
                ...result,
                data: resolveData(result.data),
            };
        },
        toSubmitDetail(id) {
            window.open(`https://registration-apply.oss-cn-hangzhou.aliyuncs.com/user-detail/${id}`);
        },
        userInfoFilterHandler(value, row, column) {
            return row[column.property] === value;
        },
    },
};
</script>

<style lang="scss" scoped>
</style>
