<template>
    <Provider url="/api/crash_log" :data-resolve="resolveData">
        <ElTable
            slot-scope="{ list }"
            :data="list"
            stripe
            style="width: 100%"
            :default-sort="{prop: 'time', order: 'descending'}"
        >
            <ElTableColumn prop="name" label="用户名" width="150" />
            <ElTableColumn
                prop="scene"
                label="场景"
                width="80"
                :filters="filterOptions.scene"
                :filter-method="userInfoFilterHandler"
            />
            <ElTableColumn
                prop="type"
                label="操作类型"
                width="100"
                :filters="filterOptions.type"
                :filter-method="userInfoFilterHandler"
            />
            <ElTableColumn prop="time" label="时间" width="140" sortable>
                <div slot-scope="{ row }">{{ row.time | date }}</div>
            </ElTableColumn>
            <ElTableColumn label="错误信息" width="900" sortable>
                <pre slot-scope="{ row }" style="font-size: 12px;overflow: auto;color: #777777;">{{ row.error }}</pre>
            </ElTableColumn>
        </ElTable>
    </Provider>
</template>

<script>
import { FILTER_OPTIONS } from '../constant/filterOptions';
import { resolveData } from '../util/business';

export default {
    name: 'Crash',

    data() {
        return {
            currentCrashData: [],
            filterOptions: FILTER_OPTIONS,
        };
    },

    methods: {
        resolveData(result) {
            return {
                ...result,
                data: resolveData(result.data),
            };
        },
        userInfoFilterHandler(value, row, column) {
            return row[column.property] === value;
        },
    },
};
</script>

