<template>
    <div class="index">
        <Daily />
        <ElTabs :value="String($route.params.page)" @tab-click="handleTabClick">
            <ElTabPane label="用户列表" name="user">
                <UserList />
            </ElTabPane>
            <ElTabPane label="用户详情数据" name="user_info">
                <UserInfo />
            </ElTabPane>
            <ElTabPane label="线上故障日志" name="crash">
                <Crash />
            </ElTabPane>
        </ElTabs>
    </div>
</template>

<script>
import Daily from './daily';
import UserList from './user-list';
import UserInfo from './user-info';
import Crash from './crash';

export default {
    name: 'Index',

    components: { Daily, UserList, UserInfo, Crash },

    data() {
        return {
        };
    },

    watch: {
        $route: {
            deep: true,
            handler() {
                if (this.$route.params.page) return;
                this.$router.push({
                    path: '/index/user',
                });
            },
        },
    },

    methods: {
        handleTabClick(tab) {
            this.$router.push({
                path: `/index/${tab.name}`,
            });
        },
    },
};
</script>

