<template>
    <ElTable :data="dailyList" stripe size="mini" style="width: 100%">
        <ElTableColumn prop="date" label="日期" width="180" />
        <ElTableColumn prop="count" label="活跃用户数" width="220" />
    </ElTable>
</template>

<script>
import { DAY_MS } from '../constant/time';
import { getJSONData } from '../libs/oss';
import { createFromN2N } from '../libs/util';

export default {
    name: 'Daily',

    props: {

    },

    data() {
        return {
            dailyList: [],
        };
    },
    created() {
        // this.calcDailyActiveUser();
    },

    methods: {
        // 计算列表里的当日日活
        calcUserTableActiveUser({ date, result }) {
            if (!result || !Array.isArray(result.data)) return 0;
            const start = new Date(date).getTime();
            const end = start + DAY_MS;
            return result.data.filter(item => item.start >= start && item.start <= end).length;
        },
        async calcDailyActiveUser() {
            const resultList = await Promise.all(createFromN2N(0, 7).map(i => this.getDateData(i)));
            this.dailyList = resultList.map(item => ({
                date: item.date.replace(/\d+\//, ''),
                count: this.calcUserTableActiveUser(item),
            })).reverse();
        },
        async getDateData(i) {
            const lastDate = new Date(Date.now() - DAY_MS * i).toLocaleDateString();
            const lastKey = `user-date/${lastDate}`;
            const result = await getJSONData(lastKey);
            if (typeof result === 'object') {
                return {
                    date: lastDate,
                    result,
                };
            }
            return {
                date: lastDate,
                result: null,
            };
        },
    },
};
</script>
